import {ComponentFactoryResolver, Injector, Type} from '@angular/core';
import {  EventDesignerPackageView } from './views';
import { EventDesignerComponent } from './views/eventDesigner/event.component';
import { Package, Workspace, GSP } from '@farris/ide-devkit';

export class EventDesignerPackage extends Package {

  private layoutPanel: any;

  constructor(
    gsp: GSP,
    private cfr: ComponentFactoryResolver,
    private injector: Injector
  ) {
    super(gsp);
  }

  activate(state) {
    //debugger;
    this.gsp.workspace.addOpener((uri: string) => this.openEvent(uri));
  }

  openEvent(uri) {
    //debugger;
    if (uri.endsWith('.event')) {
      const pkgView = new EventDesignerPackageView({}, this.cfr, this.injector);
      const currentElement = pkgView.getElement().instance as EventDesignerComponent;
      currentElement.metadataUri = uri;
      // urititle
      const fileName = uri.substring(uri.lastIndexOf('/') + 1);
      return {
        getTitle: () => fileName,
        getElement: () => pkgView.getElement()
      };
    }
  }
}


