import { IDataType } from "@gsp-cmp/component";
import { isNullOrUndefined } from "util";

export class Param {

    public Id: string;
    public Code: string;
    public Name: string;
    public Classification: Classification;
    public DataType: IDataType;
    public IsRequired: boolean;
    public Description: string;


    /** */
    public serialize(): Object {
        let obj = new Object();
        obj["Id"] = this.Id;
        obj["Code"] = this.Code;
        obj["Name"] = this.Name;
        obj["IsRequired"] = this.IsRequired;
        obj["Description"] = this.Description;
        obj["DataType"] = this.DataType;
        obj["Classification"] = this.Classification as number;
        return obj;
    }

    /** */
    public init(obj: Object) {
        this.Id = obj["Id"];
        this.Code = obj["Code"];
        this.Name = obj["Name"];
        this.Classification = obj["Classification"] as number;
        this.IsRequired = obj["IsRequired"] as boolean;
        this.Description = obj["Description"];
        let dataType: any = null;
        if (this.Classification == Classification.Basic) {
            dataType = new BasicType();
            dataType.init(obj["DataType"]);
        }
        else if (this.Classification == Classification.Complex) {
            dataType = new ComplexType();
            dataType.init(obj["DataType"]);
        }
        else if (this.Classification == Classification.List) {
            dataType = new ListType();
            dataType.init(obj["DataType"]);
        }
        else if (this.Classification == Classification.Dictionary) {
            dataType = new DictionaryType();
            dataType.init(obj["DataType"]);
        }
        else if (this.Classification == Classification.UnSupport) {
            dataType = new UnSupportType();
            dataType.init(obj["DataType"]);
        }
        this.DataType = dataType;

    }

    // public ParamCode: string;
    // public ParamName :string;
    // public ParamType :string;
    // public ParamDescription: string;

}

/**
 * 
 */
export interface IDataType {
    getDescription(): string;
}

/**
 * 
 */
export enum Classification {
    Basic = 0,
    List = 1,
    Dictionary = 2,
    Complex = 3,
    UnSupport = 4
}
/**
 * 
 */
export class BasicType implements IDataType {

    /** */
    ParamType: string;

    /** */
    getDescription(): string {
        if (this.ParamType == '' || isNullOrUndefined(this.ParamType)) {
            return '';
        }
        return this.ParamType;
    }

    /**
     * 
     */
    serialize(): Object {
        let obj = new Object();
        obj["ParamType"] = this.ParamType;
        return obj;
    }

    /**
     * 
     * @param obj Json
     */
    init(obj: Object) {
        this.ParamType = obj["ParamType"];
    }
}

/**
 * 
 */
export class ComplexType implements IDataType {

    /**Id */
    ModelId: string;

    /** */
    ModelName: string;

    /** */
    getDescription(): string {
        if (this.ModelName == '' || isNullOrUndefined(this.ModelName)) {
            return '';
        }
        return this.ModelName;
    }

    /**
     * 
     */
    serialize(): Object {
        let obj = new Object();
        obj["ModelId"] = this.ModelId;
        obj["ModelName"] = this.ModelName;
        return obj;
    }

    /**
    * 
    * @param obj Json
    */
    init(obj: Object) {
        this.ModelId = obj["ModelId"];
        this.ModelName = obj["ModelName"];
    }
}

/**
 * 
 */
export class DictionaryType implements IDataType {

    /**Key */
    IsTkeyBasic: boolean;

    /**Key */
    Tkey: string;

    /**KeyId */
    TkeyModelId: string;

    /**Value */
    IsTValueBasic: boolean;

    /**Value */
    TValue: string;

    /**ValueId */
    TValueModelId: string;

    /** */
    getDescription(): string {
        if (this.Tkey == '' || isNullOrUndefined(this.Tkey)) {
            return '';
        }
        if (this.TValue == '' || isNullOrUndefined(this.TValue)) {
            return '';
        }
        return `Dictionary<${this.Tkey},${this.TValue}>`;
    }

    /**
     * 
     */
    serialize(): Object {
        let obj = new Object();
        obj["IsTkeyBasic"] = this.IsTkeyBasic;
        obj["Tkey"] = this.Tkey;
        obj["TkeyModelId"] = this.TkeyModelId;
        obj["IsTValueBasic"] = this.IsTValueBasic;
        obj["TValue"] = this.TValue;
        obj["TValueModelId"] = this.TValueModelId;
        return obj;
    }

    /**
     * 
     * @param obj Json
     */
    init(obj: Object) {
        this.IsTkeyBasic = obj["IsTkeyBasic"] as boolean;
        this.Tkey = obj["Tkey"];
        this.TkeyModelId = obj["TkeyModelId"];
        this.IsTValueBasic = obj["IsTValueBasic"] as boolean;
        this.TValue = obj["TValue"];
        this.TValueModelId = obj["TValueModelId"];
    }
}

/**
 * 
 */
export class ListType implements IDataType {

    /** */
    IsBasic: boolean;

    /**Id */
    ModelId: string;

    /** */
    ModelName: string;

    /** */
    getDescription(): string {
        if (this.ModelName == '' || isNullOrUndefined(this.ModelName)) {
            return '';
        }
        return `List<${this.ModelName}>`;;
    }

    /**
    * 
    */
    serialize(): Object {
        let obj = new Object();
        obj["IsBasic"] = this.IsBasic;
        obj["ModelId"] = this.ModelId;
        obj["ModelName"] = this.ModelName;
        return obj;
    }

    /**
     * 
     * @param obj Json
     */
    init(obj: Object) {
        this.IsBasic = obj["IsBasic"] as boolean;
        this.ModelId = obj["ModelId"];
        this.ModelName = obj["ModelName"];
    }
}

/**
 * 
 */
export class UnSupportType implements IDataType {

    /** */
    ParamType: string;

    /** */
    getDescription(): string {
        if (this.ParamType == '' || isNullOrUndefined(this.ParamType)) {
            return '';
        }
        return this.ParamType;
    }

    /**
     * 
     */
    serialize(): Object {
        let obj = new Object();
        obj["ParamType"] = this.ParamType;
        return obj;
    }

    /**
     * 
     * @param obj Json
     */
    init(obj: Object) {
        this.ParamType = obj["ParamType"];
    }
}




