import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http'
import { CommonModule } from '@angular/common';
import { ButtonsModule } from '@progress/kendo-angular-buttons';
import { EventDesignerComponent } from './event.component';
import { GridModule } from '@progress/kendo-angular-grid';
import { LayoutModule } from '@progress/kendo-angular-layout';
import { ParamsInfoComponent } from './paramsInfo/paramsInfo';
import { BasicInfoComponent } from './basicInfo/basicInfo';
import { HttpClient, HttpClientModule } from '@angular/common/http';
import { DropDownsModule } from '@progress/kendo-angular-dropdowns';
import { ToolBarModule } from '@progress/kendo-angular-toolbar';
import { ParamService } from './paramsInfo/param.service';
import { DialogModule } from '@progress/kendo-angular-dialog';
import { NewParamModalComponent } from './paramsInfo/parammodal/newparammodal.component';
import { EditParamModalComponent } from './paramsInfo/parammodal/editparammodal.component';
import { BasicTypeModalComponent } from './paramsInfo/parammodal/datatypemodal/basic/basictypemodal.component';
import { ComplexTypeModalComponent } from './paramsInfo/parammodal/datatypemodal/complex/complexmodal.component';
import { ListTypeModalComponent } from './paramsInfo/parammodal/datatypemodal/list/listmodal.component';
import { DictionaryTypeModalComponent } from './paramsInfo/parammodal/datatypemodal/dictionary/dictionarymodal.component';
@NgModule({
  declarations: [
    EventDesignerComponent,
    ParamsInfoComponent,
    BasicInfoComponent,
    NewParamModalComponent,
    BasicTypeModalComponent,
    ListTypeModalComponent,
    ComplexTypeModalComponent,
    DictionaryTypeModalComponent,
    EditParamModalComponent
  ],
  imports: [
    CommonModule,
    FormsModule,
    HttpClientModule,
    DialogModule,
    ButtonsModule,
    GridModule,
    LayoutModule,
    DropDownsModule,
    ToolBarModule
  ],
  providers: [ParamService],
  entryComponents:[BasicTypeModalComponent,
    ListTypeModalComponent,
    ComplexTypeModalComponent,
    DictionaryTypeModalComponent,EditParamModalComponent,NewParamModalComponent],
  bootstrap: [EventDesignerComponent]
})
export class EventDesignerModule { }



