import { Observable } from 'rxjs/Observable';
import { Component, OnInit, Inject, Input, EventEmitter } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { NewParamModalComponent } from './parammodal/newparammodal.component';
import {EditParamModalComponent} from './parammodal/editparammodal.component';
import { GridDataResult, SelectAllCheckboxState, RowArgs } from '@progress/kendo-angular-grid';
import { State, process } from '@progress/kendo-data-query';

import { Param } from '../models/Param';

import { map } from 'rxjs/operators/map';
import { EbsEvent } from '../models/ebsEvent';
import { mockEbsEvent } from '../mock/mockService';
import { DialogService, DialogCloseResult, DialogAction } from '@progress/kendo-angular-dialog';
import { ParamService } from './param.service';
@Component({
    selector: 'params-info',
    templateUrl: './paramsInfo.html',
    styleUrls: ['./paramsInfo.css']
})
export class ParamsInfoComponent {
    public source: Array<string> = ['Albania', 'Andorra', 'Armenia', 'Austria', 'Azerbaijan'];
    @Input() event: EbsEvent;
    // public argsView: Array<Param>;

    constructor(private service: ParamService, private dialogService: DialogService) { }
    public formGroup: FormGroup;
    private editedRowIndex: number;
    newParam: Param;
    currentParamList: Param[] = [];
    selectedParamList: Param[];
    selectedParamIds: string[] = [];
    selectAllParamState: SelectAllCheckboxState = 'unchecked';
    currentParam: Param;

  
    ngOnChanges(){
        //debugger;
        if(!this.event.Parameters){
            this.event.Parameters=[] as Param[];
            this.currentParamList=this.event.Parameters;
        }
        else{
            this.currentParamList=this.event.Parameters;
        }
    }

    isRowParamSelected = (e: RowArgs) => this.selectedParamIds.indexOf(e.dataItem.Id) >= 0;

    cellClickHandler({ dataItem }) {
        this.currentParam = Object.assign(new Param(), dataItem);
        this.resetParamRowSelection(this.currentParam);
    }

    resetParamRowSelection(dataTtem: any) {
        this.selectedParamIds = [];
        this.selectedParamIds.push(dataTtem.Id);
    }

    onSelectedParamChange(e) {
        //debugger;
        this.selectedParamList = [] as Param[];
        let len = this.selectedParamIds.length;
        if (len > 0) {
            this.selectedParamIds.forEach(id => {

                let tempIndex = this.event.Parameters.findIndex(value => value.Id == id);
                if (tempIndex != (-1)) {
                    this.selectedParamList.push(this.event.Parameters[tempIndex]);
                }
            });
        }
        if (len === 0) {
            this.selectAllParamState = 'unchecked';
        } 
        else if (len > 0 && len < this.event.Parameters.length) {
            this.selectAllParamState = 'indeterminate';
        } 
        else  {
            this.selectAllParamState = 'checked';
        }
    }

    InitParam(){
        alert("")
    }

    /**
     * 
     */
    addParam() {
        const dialogRef = this.dialogService.open({
            title: '',
            content: NewParamModalComponent,
            actions: [
                { text: '' },
                { text: '', primary: true }
            ],
            width: 400,
            height: 460
        });
        this.newParam = new Param();
        //
        const intance = dialogRef.content.instance;
        intance.newParam = this.newParam;
        const actionEmitter = dialogRef.dialog.instance.action;
        dialogRef.dialog.instance.action = new EventEmitter<any>();
        var that = this;
        const sub = dialogRef.dialog.instance.action.subscribe(action => {
            if (action.text == "") {
                //debugger;
                let param: Param = that.newParam;
                if (this.checkParam(param)) {
                    actionEmitter.emit(action);
                }
            }
            else {
                actionEmitter.emit(action);
            }
        });
        //
        dialogRef.result.subscribe((dialogResult) => {
            sub.unsubscribe();
            if (!(dialogResult instanceof DialogCloseResult)) {
                let actionName = (dialogResult as DialogAction).text;
                if (actionName == "") {
                    //debugger;
                    this.newParam = Object.assign(new Param(), dialogRef.content.instance.newParam);
                    this.newParam.Id = this.service.getGuid();
                    this.currentParamList.push(this.newParam);
                    // this.event.Parameters.push(this.newParam)
                    if (this.currentParamList.length == 1) {
                        this.selectAllParamState = 'checked';
                    }
                    else if (this.currentParamList.length > 1) {
                        this.selectAllParamState = 'indeterminate';
                    }
                }
            }
        });
    }

    /**
     * 
     * @param param 
     */
    checkParam(param: Param) {
        if (!param.Code) {
            alert("")
            return false;
        }
        if (!param.Name) {
            alert("")
            return false;
        }
        if (!param.DataType) {
            alert("")
            return false;
        }
        let tempIndex = this.event.Parameters.findIndex(value => value.Id != param.Id&&value.Code==param.Code);
        if(tempIndex>-1){
            alert("")
            return false;
        }
        return true;
    }

    /**
     * 
     */
    deleteParamList() {
        //debugger;
        if (!this.selectedParamList || this.selectedParamList.length <= 0) {
            alert("");
            return;
        }
        this.selectedParamList.forEach(element => {

            let tempIndex = this.currentParamList.findIndex(value => value.Id == element.Id);
            if (tempIndex != (-1)) {
                this.currentParamList.splice(tempIndex, 1);
                // this.event.Parameters.splice(tempIndex,1);
            }
            // this.service.deleteSpecifiedElement(this.selectedParamIds, element.Id);
        });

        this.selectAllParamState = 'unchecked';
    }

    onSelectAllParamChange(checkedState: SelectAllCheckboxState) {
        if (checkedState === 'checked') {
            this.selectedParamIds = this.selectedParamList.map((item) => item.Id);
            this.selectAllParamState = 'checked';
        } else {
            this.selectedParamIds = [];
            this.selectAllParamState = 'unchecked';
        }
    }

    /**
     * 
     */
    getParamClassification(param: Param): string {
        if (param.Classification == 0) {
            return 'Basic';
        }
        else if (param.Classification == 1) {
            return 'List<T>';
        }
        else if (param.Classification == 2) {
            return 'Dictionary<TKey,TValue>';
        }
        else if (param.Classification == 3) {
            return 'Complex';
        }
        else if (param.Classification == 4) {
            return 'UnSupport';
        }
        else {
            return "";
        }
    }
    /**
     *  
     */
    getParamTypeDescription(param: Param) {
        if (param && param.DataType) {
            return param.DataType.getDescription();
        }
        else {
            return '';
        }
    }

    /**
     * 
     * @param dataItem 
     */
    editParam(dataItem: any) {
        //debugger;
        this.currentParam = Object.assign(new Param(), dataItem);
        this.selectedParamList = [];
        this.selectedParamList.push(this.currentParam);
        this.resetParamRowSelection(this.currentParam);
        if (this.currentParamList.length == 1) {
            this.selectAllParamState = 'checked';
        }
        else if (this.currentParamList.length > 1) {
            this.selectAllParamState = 'indeterminate';
        }
        const dialogRef = this.dialogService.open({
            title: '',
            content: EditParamModalComponent,
            actions: [
                { text: '' },
                { text: '', primary: true }
            ],
            width: 400,
            height: 460
        });
        //
        const intance = dialogRef.content.instance;
        intance.currentParam = this.currentParam;
        if (this.currentParam && this.currentParam.DataType) {
            intance.dataTypeDescription = this.currentParam.DataType.getDescription();
        }
        else {
            intance.dataTypeDescription = null;
        }
        const actionEmitter = dialogRef.dialog.instance.action;
        dialogRef.dialog.instance.action = new EventEmitter<any>();
        var that=this;
        const sub = dialogRef.dialog.instance.action.subscribe(action => {
            if (action.text == "") {
                if (this.checkParam(that.currentParam)) {
                    actionEmitter.emit(action);
                }
            }
            else {
                actionEmitter.emit(action);
            }
        });
        //
        dialogRef.result.subscribe((dialogResult) => {
            sub.unsubscribe();
            if (!(dialogResult instanceof DialogCloseResult)) {
                let actionName = (dialogResult as DialogAction).text;
                if (actionName == "") {
                    let tempIndex = this.currentParamList.findIndex(value => value.Id == Object.assign(new Param(), dialogRef.content.instance.currentParam).Id);
                    if (tempIndex != (-1)) {
                        this.currentParam = Object.assign(new Param(), dialogRef.content.instance.currentParam)
                        this.currentParamList.splice(tempIndex, 1, this.currentParam);
                        // this.event.Parameters.splice(tempIndex,1,this.currentParam)
                    }
                }
            }
        });
    }


    // editParamConfirm() {
    //     let isExistIndex = this.currentParamList.findIndex(value => value.Code == this.currentParam.Code && value.Id != this.currentParam.Id);
    //     if (isExistIndex != (-1)) {
    //         alert("" + this.currentParam.Code + "");
    //         return;
    //     }
    //     let tempIndex = this.currentParamList.findIndex(value => value.Id == this.currentParam.Id);
    //     if (tempIndex != (-1)) {
    //         this.currentParamList.splice(tempIndex, 1, this.currentParam);
    //     }
      
    // }


















































    //
    public addHandler({ sender }) {
        //debugger;
        this.closeEditor(sender);
        this.formGroup = new FormGroup({
            'Code': new FormControl('newParam', Validators.required),
            'Name': new FormControl('', Validators.required),
            // 'ParamType': new FormControl('object', Validators.required),
            'Description': new FormControl(''),
        });

        sender.addRow(this.formGroup);

    }

    //
    public editHandler({ sender, rowIndex, dataItem }) {
        //debugger;
        this.closeEditor(sender);
        this.formGroup = new FormGroup({
            'Name': new FormControl(dataItem.Name, Validators.required),
            'Code': new FormControl(dataItem.Code),
            // 'ParamType': new FormControl(
            //     dataItem.ParamType
            // ),
            'Description': new FormControl(dataItem.Description)
        });

        this.editedRowIndex = rowIndex;

        sender.editRow(rowIndex, this.formGroup);
    }

    //
    public cancelHandler({ sender, rowIndex }) {
        //debugger;
        this.closeEditor(sender, rowIndex);
    }

    //
    public saveHandler({ sender, rowIndex, formGroup, isNew }) {
        //debugger;
        let newParam: any = formGroup.value;
        //
        if (isNew) {
            if (this.paramValidation(newParam, rowIndex)) {
                this.event.Parameters.push(newParam);
            }
            else {
                alert("")
                return;
            }
        }
        //
        else {
            if (this.paramValidation(newParam, rowIndex)) {
                this.event.Parameters[rowIndex] = newParam;
            }
            else {
                alert("")
                return;
            }
        }
        //debugger;
        sender.closeRow(rowIndex);
    }

    //
    public removeHandler({ dataItem }) {
        //debugger;
        let code: string;
        let item = this.event.Parameters.find(x => x.Code == dataItem['Code'])
        let index = this.event.Parameters.indexOf(item)
        this.event.Parameters.splice(index, 1);

    }

    private closeEditor(grid, rowIndex = this.editedRowIndex) {
        grid.closeRow(rowIndex);
        this.editedRowIndex = undefined;
        this.formGroup = undefined;
    }

    paramValidation(param: Param, rowIndex: number) {
        if (!this.event.Parameters || this.event.Parameters.length == 0) {
            this.event.Parameters = new Array<Param>();
            return true;
        }
        let index = 0;
        for (let p of this.event.Parameters) {
            if (index != rowIndex && p.Code == param.Code) {
                return false;
            }
            index++;
        }
        return true;
    }
}


