import { Observable } from 'rxjs/Observable';
import { Component, OnInit, Inject,Input } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';

import { GridDataResult } from '@progress/kendo-angular-grid';
import { State, process } from '@progress/kendo-data-query';

import { Param } from '../models/Param';

import { map } from 'rxjs/operators/map';
import { EbsEvent } from '../models/ebsEvent';
import { mockEbsEvent } from '../mock/mockService';
@Component({
  selector: 'basic-info',
  templateUrl: './basicInfo.html',
  styleUrls: ['./basicInfo.css']
})
export class BasicInfoComponent {
    @Input() event: EbsEvent;
    status:Array<string>=["",""];
    getId(){
      return this.event&&this.event.Id?this.event.Id:"";
    }

    needResponse(){
      return this.event?(this.event.NeedResponse?"":""):""
    }
    needResponseChanged( value:string){
      this.event.NeedResponse= value==""?true:false;
    }
  
}


