
import { Param } from './Param'
export class EbsEvent {

    public Id: string;
    public Code: string;
    public Name: string;
    public BizObj: string;
    public CriticalApplication: string;
    public ServiceUnit: string;
    public Version: string;
    public NeedResponse: boolean;
    public Parameters: Array<Param>;
    public Description: string;

    /** */
    public serialize(): Object {
        let obj = new Object();
        obj["Id"] = this.Id;
        obj["Code"] = this.Code;
        obj["Name"] = this.Name;
        obj["BizObj"] = this.BizObj;
        obj["CriticalApplication"] = this.CriticalApplication;
        obj["ServiceUnit"] = this.Description;
        obj["Version"] = this.Version;
        obj["NeedResponse"] = this.NeedResponse;
        obj["Description"] = this.Description;
        let paramArray = new Array<Object>();
        if (this.Parameters != null && this.Parameters.length > 0) {
            this.Parameters.forEach(param => {
                paramArray.push(param.serialize());
            });
        }
        obj["Parameters"] = paramArray;
        return obj;
    }


    /** */
    public init(obj: Object) {
        this.Id = obj["Id"];
        this.Code = obj["Code"];
        this.Name = obj["Name"];
        this.BizObj = obj["BizObj"];
        this.CriticalApplication = obj["CriticalApplication"];
        this.ServiceUnit = obj["ServiceUnit"];
        this.Version = obj["Version"];
        this.NeedResponse = obj["NeedResponse"];
        this.Description = obj["Description"];
        let paramArray = new Array<Param>();
        if (obj["Parameters"] != null) {
            obj["Parameters"].forEach(paramObj => {
                let eventParam = new Param();
                eventParam.init(paramObj);
                paramArray.push(eventParam);
            });
        }
        this.Parameters = paramArray;
    }
}






