import { Component } from '@angular/core';
import { EbsEvent } from './models/ebsEvent';
import { mockEbsEvent } from './mock/mockService';
import { Response, Headers, RequestOptions, URLSearchParams, Http } from '@angular/http';
import { host } from './services/global';
import { Param } from './models/Param';
import { GSPMetadataService, MetadataDto } from '@gsp-lcm/metadata-selector';
import { CacheService } from '@farris/ide-devkit/services';
import { isNullOrUndefined } from 'util';
import { isNullOrEmptyString } from '@progress/kendo-angular-grid/dist/es2015/utils';

@Component({
  selector: 'app-root',
  templateUrl: './event.component.html',
  styleUrls: ['./event.component.css']
})

export class EventDesignerComponent {
  constructor(private http: Http,private metadataService:GSPMetadataService,private cache:CacheService) { }
  public event: EbsEvent={} as EbsEvent;
  metadataDto: MetadataDto;
  private _metadataUri:string;
  private _metadataContnet: MetadataDto;

  set metadataUri(metadataUri: string) {
    this._metadataUri = metadataUri;
    // todo: 
    const fileName = metadataUri.substring(metadataUri.lastIndexOf('/') + 1);
    const filePath = metadataUri.substring(1, metadataUri.lastIndexOf('/'));
    this.metadataService.LoadMetadata(fileName, filePath).subscribe(value => {
      this.metadataContent = value;
    });
  }

  set metadataContent(metadataContent: any) {
    //debugger;
    this._metadataContnet = metadataContent;
    this.loadMetadata(metadataContent);
  }

  //
  private loadMetadata(dto: MetadataDto) {
    //debugger;
    this.metadataDto = dto;
    if(dto.content&&dto.content!=""){
      this.event=new EbsEvent();
      let jsonObj=JSON.parse(dto.content);
      this.event.init(jsonObj);
      // this.event = this.getEventFromContent(this.metadataDto.content);
      this.event.Code=(isNullOrUndefined(this.event.Code)||isNullOrEmptyString(this.event.Code))?dto.code:this.event.Code;
      this.event.Name=(isNullOrUndefined(this.event.Name)||isNullOrEmptyString(this.event.Name))?dto.name:this.event.Name;
    
    }
    
  }

  private getEventFromContent(content: string): EbsEvent {
    let jsonStr = JSON.parse(content);
    let eventMetadata = new EbsEvent;
    for (let key in jsonStr) {
      eventMetadata[key] = jsonStr[key];
    }
    //debugger;
    if (!eventMetadata.Parameters) {
      eventMetadata.Parameters = [] as Array<Param>;
    }
    return eventMetadata;
  }


  //
  save() {
    debugger;
    if(!this.event.NeedResponse){
      this.event.NeedResponse=false
    }
    let stringcontent = JSON.stringify(this.event);
    this.metadataDto.content = stringcontent;
    if(this.checkBeforeSave()){
    let sessionId=this.cache.get('sessionId');
    this.metadataService.SaveMetadata(this.metadataDto,sessionId).subscribe(response=>{
        if(response.status==200){
           alert("");
        }
        else{
          alert("!");
        }
    })}


  }
  checkBeforeSave(){
    if(!this.event.Code){
      alert("")
      return false;
    }
    if(!this.event.Name){
      alert("")
      return false;
    }
    if(!this.event.CriticalApplication){
      alert("")
      return false;
    }
    if(!this.event.ServiceUnit){
      alert("")
      return false ;
    }
    if(!this.event.Version){
      alert("")
      return false ;
    }
    if(this.event.Code.length>200){
      alert("")
      return false;
    }
    if(this.event.Name.length>200){
      alert("")
      return false;
    }
    if(this.event.CriticalApplication.length>200){
      alert("")
      return false;
    }
    if(this.event.ServiceUnit.length>200){
      alert("")
      return false;
    }
    if(this.event.Version.length>20){
      alert("")
      return false;
    }
    if(this.event.Description&&this.event.Description.length>2000){
      alert("")
      return false;
    }
    return true;
  }
}






